.projectCard {
    display: flex;
    justify-content: center;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    padding: 10px;
    height: auto;
    transition: 1s all;
    border-radius: 10px;
    box-shadow: 1px 1px 7px 4px var(--transparentblack);

}

.projectCard .links {
    display: flex;
    justify-content: space-evenly;
}



.linkEnabled {
    color: var(--pink);
    font-size: 40px;
    transition: 0.2s all;
}

.linkEnabled:hover {
    color: var(--lightblue);
}

.linkDisabled {
    color: var(--lightestdark);
    font-size: 40px;
}


.hiddenCardContent {
    opacity: 0;
    position: relative;
    transform: translate(0, -1%);
    transition: 0.3s all;
    text-align: center;
}

.showCardContent {
    opacity: 1;
    position: relative;
    transform: translate(0, 1%);
    transition: 0.3s all;
    text-align: center;
    height: 100%;
    background-color: var(--transparentblack);
}

@media (max-width:700px) {
    .projectCard {
        margin: 10px 0;
    }
}