


.studiesContainers
{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    animation: fadeInAnimation ease 0.5s;
    animation-iteration-count: 1;
    animation-fill-mode: forwards; 
}

.studiesContainers hr
{
    margin: 30px 0;
    width: 60%;
    background-color: var(--lightblue);
    border: none;
    height: 1px;
}